  content: "";
  height: 4.6875rem;
  inset-inline-start: -1.625rem;
  opacity: 0.15;
  position: absolute;
  inset-block-start: -3.125rem;
  transform: rotate(35deg);
  transition: all 600ms cubic-bezier(0.2, 1, 0.25, 1);
  width: 1.875rem;
  z-index: 30;
}

.btn-glare:hover:after {
  inset-inline-start: 100%;
  inset-block-start: 0;
  transition: all 600ms cubic-bezier(0.2, 1, 0.25, 1);
}

.btn-darken-hover {
